.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
}

.wrapper iframe {
    width: 56vw;
    height: 31.5vw;
    border-radius: 1vw;
    box-shadow: 0px 0px 20px 5px black;
    border-style: dashed;
}

.counter {
    width: 100vw;
    height: 20vh;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 25px;
}

.counter h1 {
    font-size: 30px;
    width: 100vw;
    background: none;
    text-shadow: 1px 1px 2px black;
}

.progressBar {
    height: 20px; width: 60vw;
    border: solid 2px white;
}

.progressBar .completed {
    background-color: white;
    height: 20px; width: 0;
}

.progressBar .completed p {
    background: none;
    color: black;
    float: left;
    margin: 3px;
}